<template>
  <div id="app">
    <Header v-show="bNav"></Header>

    <router-view>路由展示区</router-view>
    <Footer v-show="bFoot"></Footer>
    <!-- <Bottom></Bottom> -->
    <!-- <Loading></Loading> -->
  </div>
</template>

<script>
import Header from "@/components/Header";
import Footer from "./components/Footer/Footer";
import Loading from "./components/Loading/Loading.vue";
import Nav from "@/components/Nav/Nav.vue";
import Bottom from "./components/Bottom";

import {mapGetters} from "vuex";
// console.log(mapGetters,'mapgetters')

export default {
  name: "app",
  components: {
    Header,
    Footer,
    Loading,
    Nav,
    Bottom
  },
  // mounted(){
  //   console.log('mounted',999)
  // },
  watch: {
    $route: {
      // handler: this.routePathchange, //引用methods的方法

      handler({ path }) {//结构赋值 $route对象的属性
        //对应路径，相应改变内容
        console.log(path)
        if (/^(\/home)/.test(path)) {
          this.$store.dispatch('VIEW_NAV',true);
          this.$store.dispatch('VIEW_FOOT',true);
          // this.$root.$data.bHeader = true;
          // this.$root.$data.Footer = true;
        }
        if (/^(\/login|\/reg)/.test(path)) {
          this.$store.dispatch('VIEW_NAV',false);
          this.$store.dispatch('VIEW_FOOT',true);
          // this.$root.$data.bHeader = false;
          // this.$root.$data.Footer = true;
        }
        if (/^(\/search|\/colomn)/.test(path)) {
          this.$store.dispatch('VIEW_NAV',false);
          this.$store.dispatch('VIEW_FOOT',false);
          // this.$root.$data.bHeader = false;
          // this.$root.$data.Footer = false;
        }
        //待扩展
      },
      immediate: true
      // deep: true
    }
  },
  methods: {
  
  },
  computed:{
    ...mapGetters(['bNav','bFoot','bLoading'])
  }
};
</script>

